body {
    font: 30px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    color: teal;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: rgb(255, 153, 0);
    border: 4px solid rgb(255, 153, 0);
    color: teal;
    float: left;
    font-size: 45px;
    font-weight: bold;
    line-height: 100px;
    height: 100px;
    margin-right: 3px;
    margin-top: 3px;
    padding: 0;
    text-align: center;
    width: 100px;
    border-radius: 8%;
  }
  
  .square:focus {
    outline: none;
  }

  .square:hover {
    transition: background-color 0.5s ease-out;
    background-color: rgb(184, 231, 233);
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }